 body {
     margin: 0;
     padding: 0;
     font-family: sans-serif;
     background: #eee;
 }
 
 code {
     font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
 }
 
 .todo-app {
     max-width: 700px;
     margin: 20px auto;
     padding: 10px 20px 10px;
 }
 
 .collection-item {
     background: #fff;
 }
 
 .collection-item span:hover {
     cursor: pointer;
     text-decoration: line-through;
 }
 
 form {
     margin: 20px 10px;
 }